<template><markdown-wrapper><h1 id="modal">Modal <a class="header-anchor" href="#modal">&#128279;</a></h1>
<blockquote>
<p>Modals are streamlined, but flexible, dialog prompts with the minimum required functionality and smart defaults.</p>
</blockquote>
<p><ins class="adsbygoogle"
     style="display:block; text-align:center;"
     data-ad-layout="in-article"
     data-ad-format="fluid"
     data-ad-client="ca-pub-4714899946256166"
     data-ad-slot="4603582855"></ins></p>
<h2 id="example">Example <a class="header-anchor" href="#example">&#128279;</a></h2>
<p>Toggle a modal by clicking the button below. It will slide down and fade in from the top of the page.</p>
<p>A simple modal example with callback:</p>
<modal-example ref="modal-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open=true&quot;</span>&gt;</span>Launch Demo Modal<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal 1&quot;</span> @<span class="hljs-attr">hide</span>=<span class="hljs-string">&quot;callback&quot;</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">&quot;modal&quot;</span> <span class="hljs-attr">id</span>=<span class="hljs-string">&quot;modal-demo&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Text in a modal<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Duis mollis, est non commodo luctus, nisi erat porttitor ligula.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Popover in a modal<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
        This
        <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">v-popover:modal-demo</span>=<span class="hljs-string">&quot;{title:&#x27;Title&#x27;,content:&#x27;Some popover content...&#x27;}&quot;</span>&gt;</span>button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
        should trigger a popover on click.
      <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Tooltips in a modal<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">&quot;button&quot;</span> <span class="hljs-attr">v-tooltip:modal-demo</span>=<span class="hljs-string">&quot;&#x27;Tooltip&#x27;&quot;</span>&gt;</span>This link<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>and<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">&quot;button&quot;</span> <span class="hljs-attr">v-tooltip:modal-demo</span>=<span class="hljs-string">&quot;&#x27;Tooltip&#x27;&quot;</span>&gt;</span>that link<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>should have tooltips on hover.<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">hr</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Overflowing text to show scroll behavior<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in, egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus vel augue laoreet rutrum faucibus dolor auctor.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus auctor fringilla.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in, egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus vel augue laoreet rutrum faucibus dolor auctor.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus auctor fringilla.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in, egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus vel augue laoreet rutrum faucibus dolor auctor.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus auctor fringilla.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      callback (msg) {
        <span class="hljs-built_in">this</span>.$notify(<span class="hljs-string">`Modal dismissed with msg &#x27;<span class="hljs-subst">${msg}</span>&#x27;.`</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-example.vue --&gt;</span>
</code></pre>
<h2 id="optional-sizes">Optional sizes <a class="header-anchor" href="#optional-sizes">&#128279;</a></h2>
<p>Modals have two optional sizes: <code v-pre>lg</code> and <code v-pre>sm</code>.</p>
<modal-optional-sizes ref="modal-optional-sizes"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open1=true&quot;</span>&gt;</span>Large Modal<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open2=true&quot;</span>&gt;</span>Small Modal<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open1&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal Title&quot;</span> <span class="hljs-attr">size</span>=<span class="hljs-string">&quot;lg&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a large modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open2&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal Title&quot;</span> <span class="hljs-attr">size</span>=<span class="hljs-string">&quot;sm&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a small modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open1</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">open2</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-optional-sizes.vue --&gt;</span>
</code></pre>
<h2 id="custom-header">Custom header <a class="header-anchor" href="#custom-header">&#128279;</a></h2>
<ul>
<li>Use <code v-pre>title</code> slot to customize the modal title.</li>
<li>Set <code v-pre>header</code> prop to <code v-pre>false</code> to hide the modal header.</li>
<li>You can also use <code v-pre>header</code> slot to take full control of the modal header. Notice that this slot will override <code v-pre>title</code> slot since it is a completely replacement</li>
</ul>
<modal-custom-header ref="modal-custom-header"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open1=true&quot;</span>&gt;</span>HTML Title<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open2=true&quot;</span>&gt;</span>No Header<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open1&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">&quot;title&quot;</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">&quot;glyphicon glyphicon-heart&quot;</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span> Modal Title<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a modal with HTML title.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open2&quot;</span> <span class="hljs-attr">:header</span>=<span class="hljs-string">&quot;false&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a modal with no header.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open1</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">open2</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-custom-header.vue --&gt;</span>
</code></pre>
<h2 id="custom-footer">Custom footer <a class="header-anchor" href="#custom-footer">&#128279;</a></h2>
<ul>
<li>Use <code v-pre>footer</code> slot to customize the modal footer.</li>
<li>Set <code v-pre>footer</code> prop to <code v-pre>false</code> to hide the modal footer.</li>
</ul>
<modal-custom-footer ref="modal-custom-footer"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open1=true&quot;</span>&gt;</span>Custom Footer<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open2=true&quot;</span>&gt;</span>No Footer<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open1&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal Title&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a modal with custom footer.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">&quot;footer&quot;</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open1=false&quot;</span>&gt;</span>Cancel<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;warning&quot;</span>&gt;</span>Warning Action<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;danger&quot;</span>&gt;</span>Danger Action<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open2&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal Title&quot;</span> <span class="hljs-attr">:footer</span>=<span class="hljs-string">&quot;false&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a modal with no footer.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open1</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">open2</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-custom-footer.vue --&gt;</span>
</code></pre>
<h2 id="custom-button-texts-and-types">Custom button texts and types <a class="header-anchor" href="#custom-button-texts-and-types">&#128279;</a></h2>
<ul>
<li>Use <code v-pre>ok-text</code> and <code v-pre>cancel-text</code> to customize button texts</li>
<li>Use <code v-pre>ok-type</code> and <code v-pre>cancel-type</code> to customize button types</li>
</ul>
<modal-custom-button-text-and-type ref="modal-custom-button-text-and-type"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open=true&quot;</span>&gt;</span>Custom Button Text and Type<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Are you sure?&quot;</span>
           <span class="hljs-attr">ok-text</span>=<span class="hljs-string">&quot;Yes, please&quot;</span> <span class="hljs-attr">cancel-text</span>=<span class="hljs-string">&quot;No way!&quot;</span>
           <span class="hljs-attr">ok-type</span>=<span class="hljs-string">&quot;danger&quot;</span> <span class="hljs-attr">cancel-type</span>=<span class="hljs-string">&quot;warning&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Do you really want to destroy this item?<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>,
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-custom-button-text-and-type.vue --&gt;</span>
</code></pre>
<h2 id="auto-focus">Auto focus <a class="header-anchor" href="#auto-focus">&#128279;</a></h2>
<p>Auto focus on footer button with <code v-pre>data-action=&quot;auto-focus&quot;</code> attribute after modal open. By default it is the OK button.</p>
<modal-auto-focus ref="modal-auto-focus"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open=true&quot;</span>&gt;</span>Auto Focus<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal Title&quot;</span> <span class="hljs-attr">auto-focus</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Check this out! The OK button is focused now.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-auto-focus.vue --&gt;</span>
</code></pre>
<h2 id="disable-backdrop">Disable backdrop <a class="header-anchor" href="#disable-backdrop">&#128279;</a></h2>
<p>Set <code v-pre>backdrop</code> prop to <code v-pre>false</code> to disable the modal dismiss action on backdrop click.</p>
<modal-disable-backdrop ref="modal-disable-backdrop"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open=true&quot;</span>&gt;</span>Disable Backdrop<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal Title&quot;</span> <span class="hljs-attr">:backdrop</span>=<span class="hljs-string">&quot;false&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a modal that can not close by backdrop click.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-disable-backdrop.vue --&gt;</span>
</code></pre>
<h2 id="disable-animation">Disable animation <a class="header-anchor" href="#disable-animation">&#128279;</a></h2>
<p>Set <code v-pre>transition-duration</code> to <code v-pre>0</code> to disable modal animations.</p>
<modal-disable-animation ref="modal-disable-animation"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open=true&quot;</span>&gt;</span>Disable Animation<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal Title&quot;</span> <span class="hljs-attr">:transition-duration</span>=<span class="hljs-string">&quot;0&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a modal that has no transition effect.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-disable-animation.vue --&gt;</span>
</code></pre>
<h2 id="nested-modals">Nested modals <a class="header-anchor" href="#nested-modals">&#128279;</a></h2>
<p>Note that if you want modals to be real nested to each other, you have to add <code v-pre>append-to-body</code> prop to them. For example:</p>
<modal-nested ref="modal-nested"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open1=true&quot;</span>&gt;</span>Open set of nested modals<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-comment">&lt;!-- `append-to-body` not required here --&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open1&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal 1&quot;</span> <span class="hljs-attr">size</span>=<span class="hljs-string">&quot;lg&quot;</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">&quot;modal1&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a simple large modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Click on the button below to open a nested modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;info&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open2=true&quot;</span>&gt;</span>Open Modal 2<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
      <span class="hljs-comment">&lt;!-- `append-to-body` required, because this is a nested modal --&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open2&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal 2&quot;</span> <span class="hljs-attr">append-to-body</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">&quot;modal2&quot;</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a nested modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;info&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open3=true&quot;</span>&gt;</span>Open Modal 3<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
        <span class="hljs-comment">&lt;!-- `append-to-body` required, because this is a nested modal --&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open3&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal 3&quot;</span> <span class="hljs-attr">size</span>=<span class="hljs-string">&quot;sm&quot;</span> <span class="hljs-attr">append-to-body</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">&quot;modal3&quot;</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is another nested modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open1</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">open2</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">open3</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-nested.vue --&gt;</span>
</code></pre>
<p>Otherwise, you can simply nest them logically, without any extra settings:</p>
<modal-nested-logically ref="modal-nested-logically"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;primary&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open1=true&quot;</span>&gt;</span>Open set of nested modals (logically)<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open1&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal 1&quot;</span> <span class="hljs-attr">size</span>=<span class="hljs-string">&quot;lg&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a simple large modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Click on the button below to open a nested modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;info&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open2=true&quot;</span>&gt;</span>Open Modal 2<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open2&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal 2&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is a nested modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">&quot;info&quot;</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">&quot;open3=true&quot;</span>&gt;</span>Open Modal 3<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;open3&quot;</span> <span class="hljs-attr">title</span>=<span class="hljs-string">&quot;Modal 3&quot;</span> <span class="hljs-attr">size</span>=<span class="hljs-string">&quot;sm&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>This is another nested modal.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">modal</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">open1</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">open2</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">open3</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- modal-nested-logically.vue --&gt;</span>
</code></pre>
<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference">&#128279;</a></h1>
<h2 id="modal-1"><a href="https://github.com/uiv-lib/uiv/blob/0.x/src/components/modal/Modal.vue">Modal</a> <a class="header-anchor" href="#modal-1">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Show / hide the modal.</td>
</tr>
<tr>
<td><code v-pre>title</code></td>
<td>String</td>
<td></td>
<td></td>
<td>The modal title (will be override if title slot exist).</td>
</tr>
<tr>
<td><code v-pre>size</code></td>
<td>String</td>
<td></td>
<td></td>
<td>The alternative modal size. Support <code v-pre>lg</code> / <code v-pre>sm</code>.</td>
</tr>
<tr>
<td><code v-pre>backdrop</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Dismiss the modal by backdrop click.</td>
</tr>
<tr>
<td><code v-pre>footer</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Show modal footer.</td>
</tr>
<tr>
<td><code v-pre>header</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Show modal header.</td>
</tr>
<tr>
<td><code v-pre>dismiss-btn</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Display the dismiss button in header.</td>
</tr>
<tr>
<td><code v-pre>cancel-text</code></td>
<td>String</td>
<td></td>
<td></td>
<td>Override the text of cancel button.</td>
</tr>
<tr>
<td><code v-pre>cancel-type</code></td>
<td>String</td>
<td>default</td>
<td></td>
<td>(0.26.0+) Button type of cancel button.</td>
</tr>
<tr>
<td><code v-pre>ok-text</code></td>
<td>String</td>
<td></td>
<td></td>
<td>Override the text of ok button.</td>
</tr>
<tr>
<td><code v-pre>ok-type</code></td>
<td>String</td>
<td>primary</td>
<td></td>
<td>(0.26.0+) Button type of ok button.</td>
</tr>
<tr>
<td><code v-pre>transition-duration</code></td>
<td>Number</td>
<td>150</td>
<td></td>
<td>Transition time of the modal, set to 0 to disable animation.</td>
</tr>
<tr>
<td><code v-pre>auto-focus</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Focus on the action button that has <code v-pre>data-action=&quot;auto-focus&quot;</code> attribute after modal open, by default it is the OK button.</td>
</tr>
<tr>
<td><code v-pre>keyboard</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Close the modal after <code v-pre>esc</code> key pressed.</td>
</tr>
<tr>
<td><code v-pre>append-to-body</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Append the modal element to <code v-pre>&lt;body&gt;</code>.</td>
</tr>
<tr>
<td><code v-pre>before-close</code></td>
<td>Function</td>
<td></td>
<td></td>
<td>Call with the <code v-pre>msg</code> param, return <code v-pre>false</code> to interrupt the modal hiding process. Promise supported since 0.34.1.</td>
</tr>
</tbody>
</table></div><h3 id="slots">Slots <a class="header-anchor" href="#slots">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>title</code></td>
<td>Replace as the modal title.</td>
</tr>
<tr>
<td><code v-pre>default</code></td>
<td>Replace as the modal body.</td>
</tr>
<tr>
<td><code v-pre>header</code></td>
<td>Replace as the modal header. Note: this slot will override <code v-pre>title</code> slot since it is a completely replacement of header.</td>
</tr>
<tr>
<td><code v-pre>footer</code></td>
<td>Replace as the modal footer.</td>
</tr>
</tbody>
</table></div><h3 id="events">Events <a class="header-anchor" href="#events">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Params</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>show</code></td>
<td></td>
<td>Fire after modal show.</td>
</tr>
<tr>
<td><code v-pre>hide</code></td>
<td>msg</td>
<td>Fire after modal dismiss with message (if exist).</td>
</tr>
</tbody>
</table></div></markdown-wrapper></template><script>
  

  

  

  

  

  

  

  

  

  
export default {components:{'modal-example':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <btn type=\"primary\" @click=\"open=true\">Launch Demo Modal</btn>\n    <modal v-model=\"open\" title=\"Modal 1\" @hide=\"callback\" ref=\"modal\" id=\"modal-demo\">\n      <h4>Text in a modal</h4>\n      <p>Duis mollis, est non commodo luctus, nisi erat porttitor ligula.</p>\n      <h4>Popover in a modal</h4>\n      <p>\n        This\n        <btn v-popover:modal-demo=\"{title:'Title',content:'Some popover content...'}\">button</btn>\n        should trigger a popover on click.\n      </p>\n      <h4>Tooltips in a modal</h4>\n      <p>\n        <a role=\"button\" v-tooltip:modal-demo=\"'Tooltip'\">This link</a>\n        <span>and</span>\n        <a role=\"button\" v-tooltip:modal-demo=\"'Tooltip'\">that link</a>\n        <span>should have tooltips on hover.</span>\n      </p>\n      <hr>\n      <h4>Overflowing text to show scroll behavior</h4>\n      <p>Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in, egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.</p>\n      <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus vel augue laoreet rutrum faucibus dolor auctor.</p>\n      <p>Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus auctor fringilla.</p>\n      <p>Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in, egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.</p>\n      <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus vel augue laoreet rutrum faucibus dolor auctor.</p>\n      <p>Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus auctor fringilla.</p>\n      <p>Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in, egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.</p>\n      <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus vel augue laoreet rutrum faucibus dolor auctor.</p>\n      <p>Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus auctor fringilla.</p>\n    </modal>\n  </section>\n</div>",
    data () {
      return {
        open: false
      }
    },
    methods: {
      callback (msg) {
        this.$notify(`Modal dismissed with msg '${msg}'.`)
      }
    }
  },'modal-optional-sizes':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <btn type=\"primary\" @click=\"open1=true\">Large Modal</btn>\n    <btn type=\"primary\" @click=\"open2=true\">Small Modal</btn>\n    <modal v-model=\"open1\" title=\"Modal Title\" size=\"lg\">\n      <p>This is a large modal.</p>\n    </modal>\n    <modal v-model=\"open2\" title=\"Modal Title\" size=\"sm\">\n      <p>This is a small modal.</p>\n    </modal>\n  </section>\n</div>",
    data () {
      return {
        open1: false,
        open2: false
      }
    }
  },'modal-custom-header':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <btn type=\"primary\" @click=\"open1=true\">HTML Title</btn>\n    <btn type=\"primary\" @click=\"open2=true\">No Header</btn>\n    <modal v-model=\"open1\">\n      <span slot=\"title\"><i class=\"glyphicon glyphicon-heart\"></i> Modal Title</span>\n      <p>This is a modal with HTML title.</p>\n    </modal>\n    <modal v-model=\"open2\" :header=\"false\">\n      <p>This is a modal with no header.</p>\n    </modal>\n  </section>\n</div>",
    data () {
      return {
        open1: false,
        open2: false
